body {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
}

.pokemon__detail-container {
  display: flex;
}

.pokemon__details-text {
  font-size: 1.5rem;
  font-style: italic;
  font-weight: 400;
  margin-bottom: 3rem;
}

.pokemon__detail {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 0.3rem;
  margin-top: 3rem;
  /* background-color: rgb(230, 230, 230); */
}

.pokemon__detail__name {
  letter-spacing: 0.2rem;
  font-weight: 600;
  padding: 1rem;
  font-size: 3rem;
}

/* pokemon splash */

.pokemon__detail-img-container {
  width: 170px;
  height: 170px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.pokeball__box {
  display: flex;
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
  position: absolute;
  margin-bottom: 20rem;
  z-index: 0;
}

.pokemon__pokeball-background {
  opacity: 0.15;
  width: 400px;
  /* z-index: -1; */
  animation: rotation 30s infinite linear;
}
@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}

.pokemon__detail__image {
  position: relative;
  width: 170px;
}

/* bounce pokemon image effect */

.imagen:hover {
  align-self: flex-end;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  margin: 0 auto 0 auto;
  transform-origin: bottom;
}
.bounce:hover {
  animation-name: bounce-7;
  animation-timing-function: cubic-bezier(0.28, 0.84, 0.42, 1);
}
@keyframes bounce-7 {
  0% {
    transform: scale(1, 1) translateY(0);
  }
  10% {
    transform: scale(1.1, 0.9) translateY(0);
  }
  30% {
    transform: scale(0.9, 1.1) translateY(-40px);
  }
  50% {
    transform: scale(1.05, 0.95) translateY(0);
  }
  57% {
    transform: scale(1, 1) translateY(-5px);
  }
  64% {
    transform: scale(1, 1) translateY(0);
  }
  100% {
    transform: scale(1, 1) translateY(0);
  }
}

/* shinny version button */

.pokemon__version {
  position: relative;
  border: none;
  padding: 0.5rem;
  border-radius: 0.2rem;
  background-color: rgb(253, 224, 93);
  font-weight: 600;
  border: 1px solid rgb(238, 199, 28);
  margin-top: 1rem;
}

.pokemon__version:hover {
  cursor: pointer;
}

/* stats */

.pokemon__stats-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1.5rem;
  background-color: white;
  padding: 2rem;
  border-radius: 0.3rem;
}

.pokemon__stats-text-container {
  display: flex;
  align-items: center;
  height: 100px;
  border-right: 1px solid rgb(201, 201, 201);
  padding-right: 1.5rem;
}

.pokemon__stats-text {
  font-weight: 500;
  font-size: 1.2rem;
}

.pokemon__stats-value-container {
  color: rgb(44, 44, 44);
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 0.4rem;
}

p {
  font-size: 0.8rem;
  font-weight: bold;
}

span {
  font-weight: lighter;
  margin-left: 5px;
}

/* back home button */

.pokemon__details-back-home {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 0.5rem;
  width: 100%;
  border: none;
  border-radius: 0.2rem;
  margin-top: 1rem;
  font-weight: 700;
  gap: 0.3rem;
  color: rgb(212, 54, 54);
}

.pokemon__details-back-home:hover {
  cursor: pointer;
}

.arrow {
  font-size: 1.5rem;
  padding-bottom: 0.3rem;
}

.pokemon__back-home-container {
  display: flex;
}

.two {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 5rem;
  margin-left: -10rem;
  z-index: 1;
}
